// colors
// TODO: Update colors here
[data-dark="false"] {
  --primary: #FCA311;
  --secondary: #14213D;
  --text: #000000;
  --background: #ffffff;
  --background-alt: #E5E5E5;
  --light-gray: #FCA311;
  --gray: #808080;
  --dark-gray: #404040;
  --overlay: #00000020;
}
// TODO: Update colors here
[data-dark="true"] {
  --primary: #FCA311;
  --secondary: #075985;
  --text: #ffffff;
  --background: #14213D;
  --background-alt: #FCA311;
  --light-gray: #FCA311;
  --gray: #E5E5E5;
  --dark-gray: #b0b0b0;
  --overlay: #ffffff10;
}

:root {
  // font families
  // TODO: Try Oswald, Lato or roboto slab
  --title: "Lato", sans-serif;
  --heading: "Lato", sans-serif;
  --body: "Lato", sans-serif;
  --code: "Roboto Mono", monospace;

  // font sizes
  --large: 1.2rem;
  --xl: 1.4rem;
  --xxl: 1.6rem;

  // font weights
  --thin: 200;
  --regular: 400;
  --semi-bold: 500;
  --bold: 600;

  // text line spacing
  --spacing: 2;
  --compact: 1.5;

  // effects
  --rounded: 3px;
  --shadow: 0 0 10px 0 var(--overlay);
  --transition: 0.2s ease;
}
